.house__img {
  width: 547px;
  height: 310px;
}

.product__button {
  background-color: #000000;
  padding: 12px;
  /* border-radius: 8px; */
  font-family: "Lato";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  margin-top: 2rem;
  color: #ffffff;
  border: none;
  cursor: pointer;
  margin-bottom: 1rem;
}
.product__button:hover {
  color: #000000;
  background-color: #ffffff;
  border: 1px solid #01518a;
  transition: all 0.2s ease-in;
}

@media screen and (min-width: 1200px) and (max-width: 1300px) {
  .house__img {
    width: 430px;
    height: 300px;
  }
}
@media screen and (max-width: 1200px) {
  .house__img {
    width: 340px;
    height: 275px;
  }
}
